@charset "UTF-8";
/* montserrat-regular - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url("../fonts/montserrat-v15-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-regular.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* montserrat-500 - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat"), url("../fonts/montserrat-v15-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-500.woff") format("woff");
  /* Modern Browsers */
  font-display: swap; }

/* Variables */
:root {
  --background-light: #EFE6E4;
  --background-darker:#E2C4BA;
  --background-dark: #8D7370;
  --background-basic: #FFFFFF;
  --fonts: #533738;
  --fonts-statistic: #FFFFFF;
  --link-request: #CC8B86;
  --number-work: #8D7370;
  --button: #CC8B86;
  --background-image: rgba(100, 72, 69, 0.8);
  --basic-white: #FFFFFF; }

.page__body {
  width: 1400px;
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat";
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: var(--fonts);
  text-transform: none;
  background-color: var(--background-light);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding-bottom: 40px; }

.page a {
  text-decoration: none; }

.page p {
  margin: 0px; }

.page img {
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.site-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.button,
.link {
  font: inherit;
  text-align: center;
  vertical-align: middle;
  text-transform: none;
  font-weight: 500;
  font-size: 20px;
  line-height: 24.38px;
  border: none; }

.button:hover,
.link:hover {
  background-color: var(--background-dark); }

.button:focus,
.link:focus {
  background-color: var(--background-dark);
  opacity: 0.5; }

.button:active,
.link:active {
  background-color: var(--background-darker); }

.button:disabled,
.link:disabled {
  cursor: default;
  opacity: 0.5;
  background-color: var(--background-light); }

.scroll-up-link {
  position: fixed;
  bottom: 15px;
  right: 20px;
  display: none;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  box-shadow: 0 2px 4px rgba(44, 44, 44, 0.2);
  border-radius: 30px;
  background-color: rgba(204, 139, 134, 0.4); }

.scroll-up-link:hover {
  background-color: rgba(204, 139, 134, 0.6); }

.scroll-up-link:focus {
  background-color: rgba(44, 44, 44, 0.2); }

.scroll-up-link::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-90deg);
  content: "";
  width: 25px;
  height: 10px;
  background-image: url("../img/svg/reviews-pagination.svg");
  background-size: contain;
  background-repeat: no-repeat; }

.scroll-up-link-showed {
  display: block; }

.button-up:hover {
  background-color: var(--background-dark); }

.section-title {
