.company__title {
  text-align: center;
  margin-bottom: 40px; }

.company__description {
  display: flex;
  margin: auto;
  width: 1036px;
  margin-bottom: 21px; }

.description__image {
  margin-right: 63.46px; }

.description__text:nth-child(1) {
