  margin-right: 200px;
  padding: 22px 38px 22px 38px; }

.request-button:hover {
  background-color: var(--background-light); }

.map {
  display: block; }

.intro {
  margin-left: auto;
  margin-right: auto;
  width: 1035px; }

.intro a {
  list-style: none;
  color: var(--fonts); }

.intro__slogan {
  font-size: 50px;
  line-height: 62px;
  padding-bottom: 20px; }

.intro__text {
  padding-bottom: 46px;
  font-size: 18px;
  line-height: 30px; }

.intro-link {
  background-color: var(--link-request);
