.main-nav {
  font-size: 16px;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-basis: auto;
  flex-grow: 1;
  padding-left: 88px; }

.main-nav__list {
  display: flex;
