.site-contact {
  padding-left: 35px; }

.page-header {
  background-image: url("../img/background-header.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--background-light);
  padding-top: 38px;
  padding-bottom: 190px; }
  @media (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/background-header@2x.jpg");
      background-size: 1400px auto; } }

.page-header__wrapper {
  display: flex;
  flex-direction: row;
  padding-bottom: 90px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 1200px; }

.page-header__logo {
  font-size: 16px;
  line-height: 19.5px;
  display: flex; }

.page-main {
  background-color: var(--background-light); }

.portfolio {
  margin-left: auto;
  margin-right: auto;
  width: 1200px;
  padding-top: 45px;
  padding-bottom: 66px; }

.portfolio-gallery {
  display: grid;
  grid-template-columns: 5fr 2fr 5fr;
  column-gap: 16px;
  grid-row-gap: 24px;
  margin: 0;
  padding-bottom: 38px; }

.portfolio-title {
  text-align: left;
  margin-bottom: 40px; }

.portfolio-item {
  position: relative; }

.portfolio-item:nth-child(4n + 2) {
  grid-column: 2 / -1; }

.portfolio-item:nth-child(4n + 3) {
  grid-column: 1 / 3; }

.portfolio-item:nth-child(4n + 4) {
  grid-column: 3 / -1; }

.portfolio-img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.portfolio-hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  box-sizing: border-box;
  padding-top: 86px;
  padding-right: 35px;
  padding-left: 35px;
  width: 100%;
  height: 100%;
  color: var(--basic-white);
  background-color: var(--background-image); }

.portfolio-item:hover .portfolio-hover {
  z-index: 2; }

.portfolio-heading {
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 15px;
  max-width: 270px;
  text-align: center;
  font-weight: 500;
  font-size: 30px;
  line-height: 37px; }

.portfolio-text {
  overflow-y: hidden; }

.portfolio-link {
  color: var(--fonts);
  font-weight: 500;
  font-size: 30px;
  line-height: 37px;
  margin-left: 452px; }

.portfolio-link:hover {
  color: var(--background-dark); }

.portfolio-link:focus {
  color: var(--background-darker);
  opacity: 0.5; }

.portfolio-link:active {
  color: var(--fonts); }

.reviews-wapper {
  position: relative;
  text-align: center;
  margin: 0;
  padding-top: 65px;
  padding-bottom: 100px;
  margin-left: 182px;
  margin-right: 182px; }

.reviews {
  width: 834px;
  margin: 0px auto 0px auto;
  display: grid;
  grid-template-columns: 428px 407px; }

.reviews-title {
  padding-bottom: 40px;
  margin-left: 20px; }

.reviews-text {
